* {
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  height: 100vh;
  background-image: url(https://static.vecteezy.com/system/resources/previews/014/661/403/non_2x/game-level-background-magic-road-and-fantasy-landscape-in-cartoon-style-road-green-field-and-forest-illustration-vector.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto;
}
.container .header {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.container .header h1 {
  font-size: 36px;
}
.container .header .avatar-user {
  width: 150px;
  height: 150px;
}
.container .header .avatar-user img {
  position: relative;
  width: 100px;
  height: 100px;
}
.container .header .avatar-user img:last-child {
  top: 0;
  left: 0;
  position: absolute;
}
.container .header h1 {
  font-size: 48px;
}/*# sourceMappingURL=App.css.map */